:root {
    /* --primary-color: #4BBCF4;
    --secondary-color: #61C0BF;
    --accent-color: #ffb6b9; */
    --primary-color: black;
    --secondary-color: #D4cab8;
    --accent-color: #ffa600;
    --shadow-color: rgb(221, 221, 221);
    /* OTHERS: #BBDED6, #FAE3D9 */
}

html, body {
    font-family: Raleway, sans-serif;
    height: 100%;
    /* width: 100%; */
    overflow-x: hidden;
}

span {
    font-family: Raleway, sans-serif;
    font-size: 1.25rem;
}

p {
    font-family: Raleway, sans-serif;
    font-weight: 300;
    padding: 0px;
    margin: 0px !important;
}

.header {
    font-family: Raleway, sans-serif;
    font-weight: 600;
    font-size: 36px;
    color: #D4cab8;
    color: var(--secondary-color)
}

.borderless {
    border: none !important;
}

.nav-offset {
    margin-top: 56px;
}

.center-column {
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    /* padding-left: 15vw !important;
    padding-right: 15vw !important; */
    max-width: 55vw !important;
}

.footer {
    height: 100px;
    width: 100vw;
    background-color: #D4cab8;
    background-color: var(--secondary-color);
    /* position: relative;
    bottom: 0; */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.footer-text {
    font-weight: 500px;
    text-align: center;
    font-size: 17px;
}
.home-col {
    position: relative;
    /* display: inline-block; */
    max-width: 100% !important;
    padding-right: 15px;
    padding-left: 15px;
}

.home-col-centered {
    float: none;
    margin: 0 auto;
    /* align-items: center; */
    /* justify-content: center; */
}

.profile-pic {
    height: 350px;
    width: 350px;
    border: 5px solid black;
    /* box-shadow: 0px 0px 5px 1px grey; */
    margin-bottom: 5px;
    /* box-shadow: 0px 0px 5px 1px var(--primary-color); */
}

.homeBtn {
    display: flex;
    flex-direction: row;
    border-radius: 25px !important;
    background-color: white !important;
    color: black !important;
    border: 2px solid black !important;
    margin-top: 2vh;
    align-items: center;
    justify-content: center;
    font-family: 'Raleway';
    font-weight: 500 !important;
    /* padding-top: 10px !important; */
}

.homeBtn:hover {
    background-color: black !important;
    color: white !important;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}

.homeBtn:focus {
    outline: transparent;
    border: transparent;
}

.splash-screen {
    /* height: 100vh; */
    /* width: 100vw; */
    /* margin: 100px; */
    justify-content: center;
}

.overlay {
    position: absolute;
    top: 56px;
    margin-left: auto;
    margin-right: auto;
    z-index: 10;
}

.fade-in-section {
    opacity: 0;
    -webkit-transform: translateY(20vh);
            transform: translateY(20vh);
    visibility: hidden;
    transition: opacity 0.6s ease-out, -webkit-transform 1.2s ease-out;
    transition: opacity 0.6s ease-out, transform 1.2s ease-out;
    transition: opacity 0.6s ease-out, transform 1.2s ease-out, -webkit-transform 1.2s ease-out;
    will-change: opacity, visibility;
    margin-bottom: 50px;
    margin-top: 5px;
  }

  .fade-in-section.is-visible {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
    visibility: visible;
  }
* {
    box-sizing: border-box;
}

body {
    font-family: 'Lato', sans-serif;
}

h1 {
    letter-spacing: 2px;
    text-align: center;
    text-transform: uppercase;
}

.countdown-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.countdown-item {
    color: #111;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    line-height: 30px;
    margin: 10px;
    padding-top: 10px;
    position: relative;
    width: 100px;
    height: 100px;
}

.countdown-item span {
    color: #333;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
}

.countdown-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
}
.section-header {
    font-family: 'Raleway';
    font-size: 48px;
    font-weight: 700;
    color: var(--primary-color);
}

.section-desc {
    font-family: 'Raleway';
    font-size: 24px;
    font-weight: 400;
    color: var(--secondary-color);
}

.btn {
    border-radius: 0px !important;
    background: white !important;
    border: none !important;
    /* padding: 0px !important; */
    /* width: fit-content !important; */
    /* height: fit-content !important; */
}

.btn:hover {
    background-color: #efefef !important;
    transition: .2s;
    /* border: 1px solid lightgrey !important; */
    -webkit-transform: translateY(-5px) !important;
            transform: translateY(-5px) !important;
}

.btn:focus {
    outline: none !important;
    outline-style: none !important;
    -moz-outline-style: none !important;
    box-shadow: none !important;    
}

.btn:active {
    -webkit-transform: translateY(5px) !important;
            transform: translateY(5px) !important;
}

.modal {
    font-family: Raleway;
}

.modal-btn {
    display: flex;
    flex-direction: row;
    border-radius: 5px !important;
    background-color: white !important;
    color: var(--accent-color) !important;
    border: 2px solid var(--accent-color) !important;
    margin-top: 2vh;
    align-items: center;
    justify-content: center;
    font-family: 'Raleway';
    font-weight: 500 !important;
}

.modal-btn:hover {
    background-color: var(--accent-color) !important;
    color: white !important;
    -webkit-transform: translateY(-2px) !important;
            transform: translateY(-2px) !important;
}

.modal-btn:active {
    -webkit-transform: translateY(2px) !important;
            transform: translateY(2px) !important
}

.recipe {
    padding: 25px !important;
    max-width: 250px;
    height: 100%;
}

.recipe-img {
    height: 200px;
    width: 200px;
    border-radius: 75px;
    margin-bottom: 25px;
}

.recipe-label {
    color: black;
    font-size: 22px;
    font-weight: 700;
}

.recipe-category {
    color: black;
    font-size: 16px;
    font-weight: 300;
}

.section {
    margin-bottom: 30px;
}
nav {
    font-family: 'Raleway', sans-serif;
    background-color: white;
}

.navbar {
    max-height: 56px;
}

